body {
  background-color: $body-color;
  overflow-x: hidden;
}

::selection {
  background: lighten($color: $primary-color, $amount: 10);
  color: $white;
}

/* preloader */

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

ol,
ul {
  list-style-type: none;
  margin: 0px;
}

img {
  vertical-align: middle;
  border: 0;
}

a {
  color:$primary-color;
}
a,
a:hover,
a:focus {
  text-decoration: none;

}

a,
button,
select {
  cursor: pointer;
  transition: .2s ease;

  &:focus {
    outline: 0;
  }
}

a.text-dark{
  &:hover{
    color: $primary-color !important;
  }
}

a:hover {
  color: $primary-color;
}

.slick-slide {
  outline: 0;
}

.section {
  padding-top: 80px;
  padding-bottom: 80px;

  &-title {
    margin-bottom: 80px;
    font-family: $tertiary-font;
  }
}

.bg-cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

/* overlay */

.overlay {
  position: relative;

  &::before {
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: $black;
    opacity: .5;
  }
}

.outline-0 {
  outline: 0 !important;
}

.d-unset {
  display: unset !important;
}

.bg-primary {
  background: $primary-color !important;
}

.bg-gray{
  background: $gray !important;
}

.text-primary {
  color: $primary-color !important;
}

.text-color {
  color: $text-color;
}

.text-dark {
  color: $text-color-dark !important;
}

.text-light {
  color: $text-color-light !important;
}

.mb-10 {
  margin-bottom: 10px !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

.mb-30 {
  margin-bottom: 30px !important;
}

.mb-40 {
  margin-bottom: 40px !important;
}

.mb-50 {
  margin-bottom: 50px !important;
}

.mb-60 {
  margin-bottom: 60px !important;
}

.mb-70 {
  margin-bottom: 70px !important;
}

.mb-80 {
  margin-bottom: 80px !important;
}

.mb-90 {
  margin-bottom: 90px !important;
}

.mb-100 {
  margin-bottom: 100px !important;
}

.zindex-1 {
  z-index: 1;
}

.rounded-lg{
  border-radius: 15px;
}

.overflow-hidden {
  overflow: hidden;
}

.font-secondary {
  font-family: $secondary-font !important;
}

.font-tertiary {
  font-family: $tertiary-font !important;
}

.shadow {
  box-shadow: 0px 18px 39.1px 6.9px rgba(224, 241, 255, 0.34) !important;
}

.bg-dark{
  background-color: #222222 !important;
}

/* icon */
.icon {
  font-size: 45px;
}

.icon-bg {
  height: 100px;
  width: 100px;
  line-height: 100px;
  text-align: center;
}

.icon-light {
  color: #c8d5ef;
}

/* /icon */

/* slick slider */
.slick-dots{
  text-align: center;
  padding-left: 0;
  li{
    display: inline-block;
    margin: 2px;
    &.slick-active{
      button{
        background: $white;
        width: 25px;
      }
    }
    button{
      height: 6px;
      width: 12px;
      background: rgba($color: $white, $alpha: .5);
      color: transparent;
      border-radius: 10px;
      overflow: hidden;
      transition: .2s ease;
      border: 0;
    }
  }
}
/* /slick slider */

/* form */
.form-control{
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid $border-color;
  height: 60px;
  &:focus{
    border-color: $primary-color;
    outline: 0;
    box-shadow: none !important;
  }
}

textarea.form-control{
  height: 120px;
}
/* /form */

/* card */
.card{
  border: 0;
  &-header{
    border: 0;
  }
  &-footer{
    border: 0;
  }
}
/* /card */
.timeline {
	text-align: center;

	.year {
		display: inline-block;
		font-size: 36px;
		font-weight: bold;
		position: relative;

		span {
			display: inline-block;
			padding-bottom: 6px;  
		}

		&:after {
			content: "";
			display: block;
			width: 80%;
			margin: auto;
			height: 1px;
			background: green;
		}

		&--end {

			&:before {
				content: "";
				display: block;
				width: 80%;
				margin: auto;
				height: 1px;
				background: green;
			}

			&:after {
				content: none;
			}
		}
	}

	.days {
		list-style-type: none;
		margin: 0;
		padding: 0;

		.day {
			width: 100%;
			float: left;

			.events {
				position: relative;
				float: left;
				border-right: 1px solid green;
				padding: 30px;
				text-align: right;
				width: 50%;

				&:after {
					content: "";
					width: 40%;
					display: inline-block;
					height: 1px;
					background: green;

					position: absolute;
					right: 0;
				}

				.date {
					position: absolute;
					top: 50%;
					left: 100%;
					transform: translateY(-50%);

					padding: 30px;
					text-align: left;

					color: green;
					font-size: 24px;
					white-space: nowrap;
				}

				.day__img {
					margin-right: -30px;
					position: relative;
					overflow: hidden;

					img {
						display: block;
						float: right;
					}

					.caption {
						position: absolute;
						margin: 0;

						bottom: 0;
						right: 0;
						padding: 20px;
						background: rgba(#000, 0.7);
						color: #fff;
					}
				}
			}

			&:nth-child(odd) {
				.events {
					width: calc(50% + 1px);
					float: right;
					border-right: 0;
					border-left: 1px solid green;
					text-align: left;

					&:after {
						right: auto;
						left: 0;
					}

					.date {
						left: auto;
						right: 100%;
						text-align: right;
					}

					.day__img {
						margin-right: auto;
						margin-left: -30px;

						img {
							float: left;
						}

						.caption {
							right: auto;
							left: 0;
						}
					}
				}
			}
		}
	}
}
